import React from 'react';
import {Link} from 'react-router-dom';

export default class AdminLogout extends React.Component
{

	componentDidMount()
	{
		localStorage.clear()
		this.props.history.push("/")
	}
	render()
	{
		return (
				<div>
					<div class="jumbotron">
						<div class="container">
							<h1>Hello, Admin</h1>
							<p>logout sucecssfully</p>
							<p>
								<a class="btn btn-primary btn-lg">Login Again</a>
							</p>
						</div>
					</div>

				</div>
			)
	}
}