import React from 'react';
import {Link} from 'react-router-dom';
import Header from './Header';
const url=require("../url");
export default class C1 extends React.Component
{
	state={a:[]}
	constructor(props)
	{
		super(props)
		fetch(`${url}?option=team-index`)
		.then(d=>d.json())
		.then(d=>this.setState({a:d}))
	}
	componentDidMount()
	{
		if(!localStorage.getItem("admin"))
		{
			this.props.history.push("/")
		}
	}
	render()
	{
		return (
				<div class="container">
					<Header />
					<table class="table table-hover">
						<thead>
							<tr>
								<th>id</th>
								<th>name</th>
								<th>email</th>
								<th>phone</th>
								<th>address</th>
								<th>dt</th>
								<th>actions</th>
								<th>more</th>
							</tr>
						</thead>
						<tbody>
							{this.state.a.map((item,index)=>
								<tr key={index}>
									<td>{item.id}</td>
									<td>{item.name}</td>
									<td>{item.email}</td>
									<td>{item.phone}</td>
									<td>{item.address}</td>
									<td>{item.created_on}</td>
									<td>actions</td>
									<td><Link to={`/admin/team/leads/${item.id}/${item.name}`}>view {item.c}</Link></td>
								</tr>
							)}
						</tbody>
					</table>
				</div>
			)
	}
}