import React from 'react';
const url=require("./url");
export default class Login extends React.Component
{
	state={email:"",password:"",error:""}
	constructor(props)
	{
		super(props)
		this.emailUpdate=this.emailUpdate.bind(this)
		this.passwordUpdate=this.passwordUpdate.bind(this)
		this.admin=this.admin.bind(this)
		this.team=this.team.bind(this)
	}
	emailUpdate(e)
	{
		this.setState({email:e.target.value.trim()})	
	}
	passwordUpdate(e)
	{
		this.setState({password:e.target.value.trim()})	
	}
	admin()
	{
		let x=this.state
		fetch(`${url}?option=admin-login&email=${x.email}&password=${x.password}`)
		.then(d=>d.json())	
		.then(d=>{
			if(d.status==="good")
			{
				localStorage.setItem("admin",true)
				this.setState({error:"please wait logged as admin redirecting to admin panel"})
				setTimeout(()=>{
					this.props.history.push("/admin/index")
				},2000)
				
			}
			else
			{
				localStorage.setItem("admin",false)
				this.setState({error:"unable to login as admin please try again"})
			}
		})	
	}
	team()
	{
		let x=this.state
		fetch(`${url}?option=admin-login&email=${x.email}&password=${x.password}`)
		.then(d=>d.json())	
		.then(d=>{
			if(d.status==="good")
			{
				localStorage.setItem("team",true)
				localStorage.setItem("id",d.id)
				localStorage.setItem("name",d.name)
				this.setState({error:"please wait logged as team redirecting to team panel"})
				setTimeout(()=>{
					this.props.history.push("/team/index")
				},2000)

			}
			else
			{
				this.setState({error:"unable to login as team please try again"})
				localStorage.clear()
			}
		})	
	}
	
	render()
	{
		let msg=this.state.error?(
					<div class="alert alert-danger">
						<button type="button" class="close" data-dismiss="alert" aria-hidden="true">&times;</button>
						<strong>{this.state.error}</strong> 
					</div>

			):null
		return (
				<div class="container">
					{msg}
					<div class="modal show" style={{position:"static"}}>
						<div class="modal-dialog modal-sm">
							<div class="modal-content">
								<div class="modal-header">
									<button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
									<h4 class="modal-title">Modal title</h4>
								</div>
								<div class="modal-body">
									<p>username</p>		
									<input class="form-control" value={this.state.email} placeholder="username" onChange={this.emailUpdate} />							
									<p>password</p>									
									<input class="form-control" value={this.state.password} placeholder="username" onChange={this.passwordUpdate} />							
								</div>
								<div class="modal-footer">
									<button onClick={this.admin} type="button" class="btn btn-default" >admin</button>
									<button onClick={this.team} type="button" class="btn btn-default" >team</button>
								</div>
							</div>
						</div>
					</div>
				</div>
			)
	}
}