import React from 'react';
import {Link} from 'react-router-dom';
import Header from './Header';
const url=require("../url");
export default class C2 extends React.Component
{
	state={a:[]}
	componentDidMount()
	{

		if(!localStorage.getItem("admin"))
		{
			this.props.history.push("/")
		}
		else
		{
			this.abc()
		}
	}
	abc()
	{
		fetch(`${url}?option=assigned`)
		.then(d=>d.json())
		.then(d=>this.setState({a:d}))
	}
	render()
	{
		return (
				<div className="container-fluid">
					<Header />
					<div>total assigned leads {this.state.a.length}</div>	
					<table class="table table-hover">
						<thead>
							<tr>
								<th>id</th>
								<th>name</th>
								<th>email</th>
								<th>phone</th>
								<th>source</th>
								<th>course</th>
								<th>tags</th>
								<th>category</th>
								<th>country</th>
								<th>city</th>
								<th>state</th>
								<th>arrived_on</th>
								<th>assigned_on</th>
								<th>to</th>
								<th>work</th>
							</tr>
						</thead>
						<tbody>
							{this.state.a.map((x,i)=>
								<tr key={i}>
									<td>{x.id}</td>
									<td>{x.name}</td>
									<td>{x.email}</td>
									<td>{x.phone}</td>
									<td>{x.source}</td>
									<td>{x.course}</td>
									<td>{x.tags}</td>
									<td>{x.category}</td>
									<td>{x.country}</td>
									<td>{x.city}</td>
									<td>{x.state}</td>
									<td>{x.arrived_on.slice(0,10)}</td>
									<td>{x.assigned_on.slice(0,10)}</td>
									<td>{x.tname}</td>
									<td><Link to={`/admin/team/leads/${x.tid}/${x.tname}/${x.lid}/${x.name}`}>view {x.c}</Link></td>
								</tr>
							)}
						</tbody>
					</table>
				</div>
			)
	}
}