import React from 'react';
import Header from './Header';
const url=require("../url");
export default class C3 extends React.Component
{
	state={a:[],b:[],c:[],error:"",tid:0}
	constructor(props)
	{
		super(props)
		this.allnone=this.allnone.bind(this)
		this.doChange=this.doChange.bind(this)
		this.assign=this.assign.bind(this)
		this.updateState=this.updateState.bind(this)
	}
	updateState()
	{
		let a=Array.from(document.querySelectorAll(".lid:checked")).map(x=>x.value)
		this.setState({c:a})	
	}
	assign()
	{
		let tid=this.state.tid
		let a=this.state.c
		let ob={
			tid:tid,
			lid:a
		}
		let headers={
			method:"POST",
			body:JSON.stringify(ob),
			headers:{
				Accept:"application/json",
				"Content-type":"application/json"
			}
		}
		fetch(url+"?option=assign",headers)
		.then(d=>d.json())
		.then(d=>alert("assigned leads success"))
		.then(d=>document.querySelectorAll(".lid").forEach(x=>x.checked=false))
		.then(d=>this.abc())

	}
	doChange(e)
	{
		let x=e.target.value
		this.setState({tid:x})
	}
	allnone()
	{
		document.querySelectorAll(".lid").forEach(x=>x.checked=!x.checked)
		let a=Array.from(document.querySelectorAll(".lid:checked")).map(x=>x.value)
		this.setState({c:a})
	}
	componentDidMount()
	{

		if(!localStorage.getItem("admin"))
		{
			this.props.history.push("/")
		}
		else
		{
			this.abc()
		}
	}
	abc()
	{
		fetch(`${url}?option=team-index`)
		.then(d=>d.json())
		.then(d=>this.setState({a:d}))
		fetch(`${url}?option=unassigned`)
		.then(d=>d.json())
		.then(d=>this.setState({b:d}))
		this.setState({c:[]})
	}
	render()
	{
		return (
				<div className="container">
					<Header />
					<select className="btn btn-default" onChange={this.doChange}>
						<option>select a team </option>
						{this.state.a.map((x,i)=>
							<option key={i} value={x.id}>{x.name} ({x.c})</option>	
						)}

					</select>

						{this.state.c.length>0&&this.state.tid?(<button onClick={this.assign} className="btn btn-danger">assign selected {this.state.c.length}</button>):null}
					<table className="table table-hover">
						<thead>
							<tr>
								<th onClick={this.allnone}>id</th>
								<th>name</th>
								<th>email</th>
								<th>phone</th>
								<th>source</th>
								<th>course</th>
								<th>tags</th>
								<th>category</th>
							</tr>
						</thead>
						<tbody>
							{this.state.b.map((item,index)=>
								<tr key={index}>
									<td>
										<input onClick={this.updateState} type="checkbox" value={item.id} className="lid"  />	
									</td>
									<td>{item.name}</td>
									<td>{item.email}</td>
									<td>{item.phone}</td>
									<td>{item.source}</td>
									<td>{item.course}</td>
									<td>{item.tags}</td>
									<td>{item.category}</td>
								</tr>
							)}
						</tbody>
					</table>
				</div>
			)
	}
}