import React from 'react';
import {BrowserRouter as Router,Route} from 'react-router-dom';

import Login from './Login';
import C1 from './admin/C1';
import C2 from './admin/C2';
import C3 from './admin/C3';
import C4 from './admin/C4';
import C5 from './admin/C5';
import AdminLogout from './admin/AdminLogout';
export default class App extends React.Component
{
	render()
	{
		return (
				<Router>
					<Route exact path="/" component={Login} />
					<Route exact path="/login" component={Login} />
					<Route exact path="/admin/index" component={C1} />
					<Route exact path="/admin/team/leads/:tid/:tname" component={C4} />
					<Route exact path="/admin/team/leads/:tid/:tname/:lid/:lname" component={C5} />
					<Route exact path="/admin/assigned" component={C2} />
					<Route exact path="/admin/unassigned" component={C3} />
					<Route exact path="/admin/logout" component={AdminLogout} />
				</Router>
			)
	}
}